.menu-btn {
  position: relative;
  width: 3vw;
  min-width: 25px;
  height: 26px;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  z-index: 101; }
  .menu-btn span {
    display: block;
    position: absolute;
    height: 2.5px;
    width: 100%;
    background: #dadbdb;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: 0.25s ease-in-out; }
    .menu-btn span:nth-child(1) {
      top: 3px; }
    .menu-btn span:nth-child(2) {
      top: 10px; }
    .menu-btn span:nth-child(3) {
      top: 17px; }
  .menu-btn.open span {
    background: #232425; }
  .menu-btn.open span:nth-child(1) {
    top: 10px;
    transform: rotate(140deg); }
  .menu-btn.open span:nth-child(2) {
    opacity: 0;
    left: -30px; }
  .menu-btn.open span:nth-child(3) {
    top: 10px;
    transform: rotate(-140deg); }

.menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  transform: translateX(100vw);
  background-color: transparent;
  transition: all 0.25s ease-in-out;
  z-index: 100; }
  .menu img {
    position: absolute;
    bottom: 0;
    left: 100%;
    max-height: 95%;
    transform: scale(0.5);
    transition: 0.2s ease 0.2s; }
  .menu ul {
    margin: 10vh auto auto 50%;
    position: relative;
    z-index: 101;
    line-height: 1.6; }
    .menu ul .t-8 {
      margin-top: 40px; }
    @media screen and (max-width: 480px) {
      .menu ul {
        margin: 5vh 5vw; }
        .menu ul .t-8 {
          margin-top: 15px; } }
  .menu.open {
    transform: translateX(0);
    background-color: #dadbdb; }
    .menu.open img {
      transform: translateX(-95vw) scale(1); }
      @media screen and (max-width: 480px) {
        .menu.open img {
          max-height: 85%;
          transform: translateX(-90vw); } }

